.level {
    padding: 30px 0;
    background-image: linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);
    width: 100%;
    margin-bottom: 40px;
    border-radius: 30px;
    height: 140px;
    transition: 0.3s;
    -webkit-border-radius: 30px;
    -moz-border-radius: 30px;
    -ms-border-radius: 30px;
    -o-border-radius: 30px;
    -webkit-transition: 0.3s;
    -moz-transition: 0.3s;
    -ms-transition: 0.3s;
    -o-transition: 0.3s;
    overflow: hidden;

    &__flex-wrapper {
        display: flex;
        align-items: center;
        width: 88%;
        margin: 0 auto;
    }

    &__img {
        width: 80px;
        height: 80px;
        border-radius: 20px;
        border: 1px solid #999;
        margin-right: 25px;
    }

    &__info {
        margin-right: auto;
    }

    &__number {
        font-weight: 700;
        font-size: 20px;
    }

    &__title {
        font-size: 18px;
    }

    &__button {
        min-width: 25%;
        height: 40px;
        padding: 0 5px;
        border: 1px solid #999;
        border-radius: 5px;
        background-color: #228B22;
        box-shadow: 2px 3px 2px #999;
        color: #fff;
        font-size: 18px;
        outline: 0;
        font-family: 'Montserrat', sans-serif;

        &:hover {
            background-color: #006400;
            cursor: pointer;
        }
    }
    
    &__list {
        padding-top: 30px;
        margin-left: 6%;
        height: auto;
    }
    
    &--active {
        height: auto;
    }
}

@import './Level.media.scss';