@media (max-width: 1050px) {
    .intro {
        margin: 30px 0;

        &__image--box {
            margin: 0 auto;
            margin-bottom: 20px;
        }

        &__content {
            width: 100%;
            margin: 0 auto;
            text-align: center;
        }

        &__title {
            font-size: 26px;
            line-height: 32px;
            margin-bottom: 20px;
        }

        &__text {
            font-size: 18px;
            line-height: 21px;
            margin-bottom: 30px;
        }
    }

    .games {
        margin: 80px 0 80px;

        &__title {
            margin-bottom: 50px;
        }

        &__item {
            margin-bottom: 30px;
        }

        &__item--title {
            font-size: 18px;
        }

        &__item--text {
            font-size: 16px;
        }
    }
}

@media (max-width: 1024px) {
    .Landing__btns {
        display: none;
    }
}

@media (max-width: 900px) {
    .intro {
        &__title {
            padding-top: 80px;
        }

        &__image--box {
            max-width: 50%;
        }

        &__content {
            width: 50%;
        }

    }

    .Landing__btns {
        display: none;
        width: 0;
    }

    .nav-link-reset {
        margin-bottom: 20px;
    }

    .preview {
        &__content {
            margin: 20% 75px 0;
            margin-top: 30%;
            padding-top: 54px;
            display: flex;
            flex-wrap: wrap;
            flex-direction: column;
        }

        &__txt {
            font-size: 30px;
            line-height: 37px;
            text-align: center;
            margin-bottom: 22px;
        }

        &__img {
            top: 140px;
            left: 30%;
        }
    }

    .Landing__btn {
        font-size: 18px;
        margin-bottom: 22px;
        border-radius: 5px;
        &:last-child {
            margin-bottom: 0;
        }
    }

    .games {
        margin: 230px 0 80px;

        &__laptop {
            margin-top: 70%;
        }

        &__title {
            display: none;

            &--new {
                min-width: 70%;
                text-align: left;
                display: block;
                font-size: 26px;
                line-height: 32px;
                font-family: 'Montserrat';
                font-weight: 700;
            }
        }

        .column {
            flex-direction: column;
            justify-content: flex-start;
        }

        &__col {
            min-width: 70%;
            margin: 0 auto;

            &:nth-child(2) {
                order: 10;
                min-width: 0;
                margin-top: -130px;
            }

            &:nth-child(1) {
                order: 20;
            }
            
            &:nth-child(3) {
                order: 30;
            }

            &:nth-child(4) {
                order: 11;
            }
        }
        
        &__item--title {
            font-size: 24px;
            line-height: 28px;
            margin-bottom: 10px;
        }

        &__title--new {
            padding-top: 50px;
            font-size: 26px;
        }

        &__item--text {
            font-size: 18px;
            line-height: 21px;
        }

        &__item--text,
        &__item--title {
            margin-left: 15%;
        }
    }
}

@media (max-width: 768px) {
    .Landing-nav__link {
        display: none;
    }

    .Landing .burger {
        display: block;
    }

    .games {
        &__item--title {
            font-size: 20px;
            line-height: 23px;
        }

        &__title--new {
            font-size: 22px;
            line-height: 27px;
        }
        
        &__item--text {
            font-size: 14px;
            line-height: 16px;
        }
    }

    .for {
        &__item {
            flex-direction: column;
            padding: 80px 0;

            &--content {
                margin: 0 auto;
                order: 20;
            }
        }

        &__title {
            font-size: 26px;
            line-height: 32px;
        }

        &__text {
            font-size: 18px;
            line-height: 21px;
        }

        &__img {
            &--right {
                order: 10;
                width: 40%;
                height: 40%;
            }

            &--left {
                width: 30%;
                height: 30%;
            }

            &--right,
            &--left {
                margin: 0 auto;
                margin-bottom: 20px;
            }
        }
    }
}

@media (max-width: 568px) {
    .container {
        padding: 0 15px;
    }

    .preview__content {
        margin: 20% 0 0;
    }

    .intro {
        &__title {
            padding-top: 0;
        }

        &__image--box {
            max-width: 80%;
            margin: 0 auto;
            margin-bottom: 2px;
        }

        &__content {
            width: 80%;
        }
    }

    .games {
        &__col {
            max-width: 100%;
        }

        &__item {
            &--text,
            &--title {
                margin-left: 17%;
            }

            &--title {
                font-size: 20px;
                line-height: 23px;
            }

            &--text {
                font-size: 14px;
                line-height: 16px;
            }
        }

        &__title--new {
            font-size: 22px;
            line-height: 27px;
            margin-left: 0;
        }
    }
}

@media (max-width: 375px) {
    .Landing-page {
        padding-top: 0;
    }

    .preview {
        padding: 200px 0 100px;

        &__content {
            margin-top: 30px;
            padding-top: 65px;
            margin-left: 18px;
            margin-right: 18px;
        }

        &__txt {
            font-size: 15px;
            line-height: 18px;
            margin-bottom: 30px;
            margin-top: 0;
            margin-left: 0;
            margin-right: 0;
        }

        &__img {
            top: 85px;
        }

        &__acc {
            font-size: 10px;
            line-height: 12px;
        }

        &__start {
            font-size: 15px;
            line-height: 18px;
        }

        .btn-preview {
            font-size: 14px;
            width: 60%;
            margin-left: 20%;
            height: 50px;
            margin-bottom: 25px;
        }
    }

    .intro {
        margin: 60px 0;
        &__content {
            margin: 0 auto;
        }

        &__title {
            font-size: 22px;
            line-height: 27px;
        }

        &__text {
            font-size: 14px;
            line-height: 16px;
        }
    }

    .for {
        &__title {
            font-size: 22px;
            line-height: 27px;
        }

        &__text {
            font-size: 14px;
            line-height: 16px;
        }
    }

    .try__it {
        &--text {
            font-size: 15px;
            line-height: 25px;
        }
        
        &--icon {
            font-size: 18px;
            line-height: 25px;
        }
    }
}