.Landing-page {
    overflow: hidden;
    background-color: #fff;
}

.intro {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: 100px 0;

    &__image--box {
        min-width: 30%;
    }

    &__content {
        margin-top: 40px;
        width: 65%;
        margin-left: 5%;
    }

    &__title {
        font-size: 32px;
        font-weight: 700;
        margin-bottom: 10px;
    }

    &__text {
        font-family: 'Roboto';
        font-size: 24px;
        color: #232323;
    }

    &__image--box {
        max-width: 300px;
        max-height: 300px;
    }

    &__img {
        width: 100%;
        height: 100%;
    }
}

.container__after:after {
    display: block;
    content: "";
    height: 1px;
    width: 100%;
    background-color: lightgray;
    position: absolute;
    left: 0;
    z-index: 1;
}

.container__after:last-child:after {
    width: 0;
    height: 0;
}

.games {
    margin: 200px 0 100px;

    &__title {
        text-align: center;
        font-size: 32px;
        margin-bottom: 100px;
        font-weight: 700;
        &--new {
            display: none;
        }
    }

    .column {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }

    &__col {
        max-width: 33%;
    }

    &__item {
        position: relative;
        margin-bottom: 50px;

        &--text,
        &--title {
            margin-left: 15%;
            font-family: 'Roboto';
        }

        &--title {
            font-weight: 700;
            font-size: 20px;
            margin-bottom: 5px;
        }

        &--text {
            font-family: 'Roboto';
            font-size: 18px;
            color: #232323;
        }
    }

    &__icon {
        position: absolute;
        left: 0;
        top: -10px;
        z-index: 1;
    }

    &__laptop {
        margin-top: 50%;
    }
}

.for {
    &__item {
        display: flex;
        justify-content: space-between;
        padding: 100px 0;
    }

    &__img {
        &--left {
            width: 13%;
            height: 13%;
        }

        &--right {
            width: 20%;
            height: 20%;
        }

        &--book {
            width: 15%;
            height: 15%;
        }
    }

    &__item--content {
        width: 75%;
    }

    &__title {
        font-size: 32px;
        font-weight: 700;
        margin-bottom: 5px;
    }

    &__text {
        font-size: 20px;
        color: #232323;
        font-family: 'Roboto';
    }
}

.try__it {
    margin-top: 5px;
    border: 0;
    outline: 0;
    background: 0;
    cursor: pointer;
    display: flex;
    justify-content: flex-start;
    text-decoration: none;

    &--icon {
        font-size: 26px;
        line-height: 35px;
        align-self: center;
        margin-right: 5px;
        background-image: linear-gradient(180deg, #2af598 0%, #009efd 100%);
        -webkit-background-clip: text;
        background-clip: text;
        color: transparent;
        -webkit-text-fill-color: transparent;
    }
    
    &--text {
        font-size: 26px;
        line-height: 35px;
        background-image: linear-gradient(180deg, #2af598 0%, #009efd 100%);
        -webkit-background-clip: text;
        background-clip: text;
        color: transparent;
        -webkit-text-fill-color: transparent;
    }
}
.container {
    padding: 0 15px;
    width: 100%;
    max-width: 1230px;
    margin: 0 auto;
}