$button-width: 27px;
$container-width: 50%;

@import '../../../css/mixins';

.Sidebar {
    position: fixed;
    right: 0;
    top: 0;
    width: 100%;
    height: 100%;
    visibility: hidden;
    background-color: rgba(0, 0, 0, 0.2);

    &__container {
        padding: 30px;
        z-index: -9999;
        //z-index: 9999;
        background-color: white;
        width: $container-width;
        height: 100%;
        position: absolute;
        right: 0px;
        transform: translateX(100%);
        -webkit-transform: translateX(100%);
        -moz-transform: translateX(100%);
        -ms-transform: translateX(100%);
        -o-transform: translateX(100%);
        transition: 0.3s;
        -webkit-transition: 0.3s;
        -moz-transition: 0.3s;
        -ms-transition: 0.3s;
        -o-transition: 0.3s;
    }

    &__button {
        z-index: 99999;
        position: fixed;
        right: 0;
        top: 40%;
        min-height: 94px;
        min-width: $button-width;
        border: none;
        border-radius: 5px;
        -webkit-border-radius: 5px;
        -moz-border-radius: 5px;
        -ms-border-radius: 5px;
        -o-border-radius: 5px;
        background-color: black;
        transition: 0.3s;
        -webkit-transition: 0.3s;
        -moz-transition: 0.3s;
        -ms-transition: 0.3s;
        -o-transition: 0.3s;
    }

    &__button--active {
        right: $container-width;
    }

    &__level {
        font-size: 22px;
        line-height: 26px;
        color: #393939;
        overflow: hidden;
        height: 30px;
        margin-bottom: 22px;
        transition: 0.3s;
        -webkit-transition: 0.3s;
        -moz-transition: 0.3s;
        -ms-transition: 0.3s;
        -o-transition: 0.3s;
    }

    &__level--active {
        overflow: initial;
        height: auto;
    }

    &__levels {
        list-style: none;
        padding: 24px;
        @include chess-border-radius;
        @include chess-border;
        height: 100%;
        overflow: auto;
    }

    &__level-title {
        display: flex;
    }

    &__level-title p {}

    &__level-button {
        border: none;
        width: 25px;
        height: 25px;
        margin-right: 19px;
        background: #FFE600;
        border-radius: 3px;
        -webkit-border-radius: 3px;
        -moz-border-radius: 3px;
        -ms-border-radius: 3px;
        -o-border-radius: 3px;

        .level-button__cursor {
            padding-top: 5px;
            transform: rotate(-90deg);
            -webkit-transform: rotate(-90deg);
            -moz-transform: rotate(-90deg);
            -ms-transform: rotate(-90deg);
            -o-transform: rotate(-90deg);
        }
    }

    &__exercieses {
        list-style: none;
        // transform: translateY(-100%);
        // -webkit-transform: translateY(-100%);
        // -moz-transform: translateY(-100%);
        // -ms-transform: translateY(-100%);
        // -o-transform: translateY(-100%);
    }

    transition: 0.3s;
    -webkit-transition: 0.3s;
    -moz-transition: 0.3s;
    -ms-transition: 0.3s;
    -o-transition: 0.3s;
}

.Sidebar--active {
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 9998;
    visibility: visible;

    .Sidebar__container {
        z-index: 9999;
        transform: translateX(0);
        -webkit-transform: translateX(0);
        -moz-transform: translateX(0);
        -ms-transform: translateX(0);
        -o-transform: translateX(0);
    }

    .Sidebar__button {
        right: 50%;
    }
}