.Footer {
    font-family: 'Roboto';
    border-top: 1px solid #000;
    background-color: #fff;

    &__container {
        padding: 0 15px;
        width: 100%;
        max-width: 1230px;
        margin: 0 auto;
    }

    &__inner {
        display: flex;
        justify-content: space-between;
        margin-bottom: 70px;
    }

    &__col {
        margin-bottom: 20px;

        &:nth-child(4) {
            max-width: 280px;
        }

        &--title {
            margin-top: 70px;
            margin-bottom: 30px;
            font-weight: bold;
            font-size: 20px;
            line-height: 23px;
            color: #000000;
        }
    }

    &__icon--wrapper {
        max-width: 170px;
        display: flex;
        flex-wrap: wrap;
    }

    &__col--img {
        width: 40px;
        height: 40px;
        margin-right: 20px;
        padding: 6px;
        border-radius: 10px;
        // box-shadow: 0px 3px 10px rgba(0,0,0,.25);
        margin-bottom: 32px;

        &:hover {
            cursor: pointer;
        }

        &:nth-child(1){
            background-color: #4680C2;
        }

        &:nth-child(2){
            background: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%,#d6249f 60%,#285AEB 90%);
        }

        &:nth-child(3){
            width: 43px;
            height: 42px;
            border-radius: 14px;
            padding: 0;
            // box-shadow: none;
        }

        &:nth-child(4){
            background: linear-gradient(135deg, rgba(255, 0, 0, 0.471), red);
        }

        &:nth-child(5){
            background-color: #0088CC;
            padding: 7px;
        }

        &:nth-child(6){
            background-color: #1da1f2;
            padding: 7px;
        }

        &:nth-child(3),
        &:nth-child(6){
            margin-right: 0;
        }
    }

    &__copyright {
        margin-top: 2px;

        &--title {
            font-weight: bold;
            font-size: 20px;
            line-height: 23px;
            color: #000000;
            margin-bottom: 21px;
        }

        &--text {
            font-size: 18px;
            line-height: 21px;
            color: #000000;
        }
    }

    &__partner {
        &--img {
            width: 195px;
            height: 37px;
        }

        &-wrapper {
            margin-top: 30px;
            display: flex;

        }
        &--uniq {
            &:nth-child(1) {
                width: 89px;
                height: 56px;
                margin-right: 25px;
                margin-top: 8px;
            }

            &:nth-child(2) {
                width: 89px;
                height: 73px;
            }
        }
    }
}

@import './Footer.media.scss';