.preview {
    width: 100%;
    padding: 234px 0;
    background: url('./img/bg1.png') center repeat;
    position: relative;

    &__img {
        left: 50px;
        top: 200px;
        display: block;
        max-width: 40%;
        position: absolute;
        max-height: 40%;
        background-color: #235390;
    }

    &__content {
        font-weight: 700;
        font-size: 34px;
        color: #fff;
        margin-left: 41%;
        align-content: center;
    }

    &__txt {
        background-color: #1e5291;
        margin-bottom: 80px;
        margin-left: 10%;
        margin-right: 10%;
    }

    .btn-preview {
        visibility: visible;
        width: 80%;
        height: 60px;
        color: #fff;
        font-size: 18px;
        margin-left: 10%;
        margin-bottom: 22px;
    }

    &__start {
        background-color: limegreen;
        border: 0;
    }

    &__acc {
        background-color: #235390;
        border-color: #fff;
    }

    &__start:hover {
        background-color: springgreen;
    }

    &__acc:hover {
        background-color: dodgerblue;
    }    
}