@import '../../../css/mixins';

.InfoBoard {
    width: 100%;
    padding: 0 24px;
    padding-top: 25px;
    padding-bottom: 17px;
    max-width: 401px;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;

    &__exercises {
        list-style: none;
        height: 236px;
        max-height: 236px;
        overflow: auto;
        font-size: 18px;
    }

    &__hint {
        display: flex;
        align-items: center;
        height: 107px;
        max-height: 107px;
        overflow: auto;
        text-align: center;
        font-size: 20px;
        font-weight: bold;
        margin-bottom: 20px;
        transition: 0.2s;
        -webkit-transition: 0.2s;
        -moz-transition: 0.2s;
        -ms-transition: 0.2s;
        -o-transition: 0.2s;
}

    &__hint span {
        vertical-align: middle;
    }

    &__hint--hidden {
        opacity: 0;
    }

    &__title,
    &__exercises,
    &__description {
        margin-bottom: 20px;
    }

    &__exercise-item {
        margin-bottom: 10px;
    }

    &__exercise-item p {
        margin-bottom: 5px;
    }

    &__check-button,
    &__solution-button,
    &__hint-button,
    &__try-more-button {
        border: none;
        @include chess-border-radius;
        color: white;
    }

    &__solution-button,
    &__try-more-button {
        border: none;
        color: white;
        width: 100%;
        max-width: 165px;
        border-radius: 10px;
        -webkit-border-radius: 10px;
        -moz-border-radius: 10px;
        -ms-border-radius: 10px;
        -o-border-radius: 10px;
        font-weight: bold;
        font-size: 21px;
        line-height: 25px;
    }

    &__try-more-button {
        background-color: #DB0000;
        margin-right: 27px;
    }

    &__check-button {
        background: #3EB968;
    }

    &__solution-button {
        background: #364C9C;
    }

    &__hint-button {
        width: 100%;
        font-weight: bold;
        font-size: 18px;
        line-height: 21px;
        background: #0E0E0E;
    }

    @media (max-width: 900px) {
        &__exercises {
            height: 180px;
            max-height: 180px;
        }

        &__hint {
            height: 85px;
            max-height: 85px;
            font-size: 18px;
        }

        @media (max-width: 840px) {
            &__exercises {
                height: 155px;
                max-height: 155px;
                font-size: 15px;
            }

            &__hint {
                height: 64px;
                max-height: 64px;
                font-size: 16px;
            }
        }

        @media (max-width: 770px) {
            & {
                padding-top: 13px;
                padding-left: 16px;
                padding-bottom: 10px;
            }

            &__title {
                font-size: 20px;
            }

            &__exercises {
                height: 123px;
                max-height: 123px;
                font-size: 13px;
            }

            &__hint {
                height: 64px;
                max-height: 64px;
                font-size: 14px;
                margin-bottom: 10px;
            }

            @media (max-width: 624px) {
                & {
                    padding-top: 13px;
                    padding-left: 13px;
                    padding-right: 7px;
                    padding-bottom: 10px;
                }

                &__title {
                    margin-bottom: 10px;
                }

                &__exercises {
                    height: 103px;
                    max-height: 103px;
                    margin-bottom: 7px;
                }

                @media (max-width: 550px) {
                    min-height: 295px;
                }
            }
        }
    }
}