.Dashboard {
    background-image: linear-gradient(to top, #cfd9df 0%, #e2ebf0 100%);

    .page {
        overflow: hidden;
        padding-top: 100px;
    }
    
    .container {
        padding: 0 15px;
        width: 100%;
        max-width: 1230px;
        margin: 0 auto;
    }
    
    .achievements {
        background-image: linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);
        content: '';
        display: block;
        width: 25%;
        height: 600px;
        border-radius: 30px;
        // position: fixed;
        position: absolute;
        right: 15px;
        top: 100px;
        z-index: 1;
    }
    
    .lk__content {
        width: 100%;
        display: flex;
        justify-content: space-between;
    }
    
    .lessons__list {
        width: 70%;
    }
}

@import './Dashboard.media.scss';