.ProgressBar {
    position: relative;
    width: 100%;
    height: 37px;
    background: #B9FFEE;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    margin-bottom: 27px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    line-height: 16px;
    color: #393939;
    overflow: hidden;
    // background: #20DFB1;

    &__proggress-flag {
        position: absolute;
        top: 0;
        left: 0;
        min-height: 100%;
        width: 50%;
        background: #20DFB1;
        transition: 0.9s;

    }

    & span {
        vertical-align: middle;
        z-index: 2;
    }

    @media (max-width: 770px) {
        & {
            height: 29px;
            font-size: 13px;
        }
    }
}