.Footer__link {
    background-color: transparent;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    display: block;
    cursor: pointer;
    font-size: 18px;
    line-height: 21px;
    margin-bottom: 21px;
    color: #000000;
    position: relative;

    &::after{
        content: "";
        width: 100%;
        height: 1px;
        background-color: #333;
        display: none;
        position: absolute;
        bottom: -5px;
        left: 0;
    }

    &:hover::after {
        display: block;
    }
}

@import './FooterColItem.media.scss';