.Landing {
    &__nav {
        font-size: 14px;
        text-transform: uppercase;
        background-color: #fff;
        padding: 10px 20px;
        border-bottom-left-radius: 30px;
        border-bottom-right-radius: 30px;
        border: 1px solid #235390;
        height: 65px;
        position: fixed;
        left: 0;
        top: 0;
        z-index: 100;
        width: 100%;
    }

    &-nav {
        &__inner {
        display: flex;
        justify-content: space-between;
        }

        &__link {
            margin: 0 15px;
            position: relative;
            padding-top: 10px;
            color: #000000;
            text-decoration: none;
            transition: color .2s linear;
            -webkit-transition: color .2s linear;
            -moz-transition: color .2s linear;
            -ms-transition: color .2s linear;
            -o-transition: color .2s linear;

            &:after {
                margin-top: -5px;
                content: "";
                display: block;
                width: 100%;
                height: 1px;
                opacity: 0;
                background-color: #235390;
                position: absolute;
                top: 100%;
                left: 0;
                z-index: 1;
                transition: opacity .2s linear;
            }

            &:hover {
                color: #b3995d;

                &:after {
                    opacity: 1;
                }
            }
        }
    }

    &__btns {
        width: 33%;
        display: flex;
        justify-content: space-between;
    }

    &__btn {
        width: 100%;
        text-decoration: none;
        height: 40px;
        padding: 12px 10px;
        opacity: 1;
        border-radius: 10px;
        cursor: pointer;
        line-height: 1.1;
        border: 1px solid #000000;
        font-family: 'Montserrat', sans-serif;
        text-transform: uppercase;
        font-size: 14px;
        font-weight: 700;
        transition: background .2s linear, visibility .1s linear;

        &-start {
            background-color: #235390;
            color: #fff;
        }

        &-login {
            background-color: #fff;
            color: #000000;
            &:hover {
                color: #fff;
            }
        }

        &:focus {
            outline: 0;
        }

        &:hover {
            background-color: blue;
        }
    }

    .burger {
        display: none;
        background: none;
        border: 0;
        cursor: pointer;
        padding: 9px 0;

        &__item {
            display: block;
            width: 30px;
            height: 3px;
            background-color: #235390;
            font-size: 0;
            color: transparent;
            position: relative;

            &:before,
            &:after {
                content: '';
                width: 100%;
                height: 100%;
                position: absolute;
                left: 0;
                z-index: 1;
                background-color: #235390;
            }

            &:before {
                top: -6px;
            }
            
            &:after {
                top: 6px;
            }
        }
    }
}