@media (max-width: 1050px) {
    .Footer {
        &__inner {
            flex-wrap: wrap;
        }

        &__col {
            width: 25%;
            margin-bottom: 0;

            &:nth-child(4) {
                order: 10;
                width: 100%;
                max-width: none;
            }

            &--img {
                &:nth-child(3) {
                    margin-right: 20px;
                }
            }
        }

        &__icon--wrapper {
            max-width: none;
        }
    }
}

@media (max-width: 768px) {
    .Footer {
        &__inner {
            margin-bottom: 45px;
        }

        &__col--title {
            margin-top: 45px;
            font-size: 18px;
            line-height: 21px;
        }

        &__partner {
            &--img {
                width: 151px;
                height: 29px;
            }

            &--uniq {
                &:nth-child(1) {
                    width: 70px;
                    height: 50px;
                    margin-right: 20px;
                    margin-top: 8px;
                }
    
                &:nth-child(2) {
                    width: 75px;
                    height: 65px;
                }
            }
        }
        &__copyright {
            &--title {
                font-size: 18px;
                line-height: 21px;
            }

            &--text {
                font-size: 14px;
                line-height: 16px;
            }
        }
    }
}

@media (max-width: 600px) {
    .Footer {
        &__inner {
            justify-content: space-between;
        }
        &__col {
            width: 45%;
        }
    }
}

@media (max-width: 420px) {
    .Footer {
        &__col--title,
        &__copyright--title{
            font-size: 14px;
            line-height: 16px;
        }

        &__icon--wrapper {
            max-width: 200px;
        }

        &__col--img:nth-child(3) {
            // margin-right: 30px;
        }

        &__copyright--text {
            font-size: 12px;
            line-height: 14px;
        }
    }
}

@media (max-width: 380px) {
    .Footer {
        &__partner{
            &--img {
                width: 100%;
            }
            &-wrapper {
                justify-content: space-between;
                width: 100%;
            }
            &--uniq {
                &:nth-child(1){
                    margin-right: 0;
                    max-width: 50%;
                }
                &:nth-child(2){
                    max-width: 50%;
                }
            }
        }
    }
}