.Exerciese {
    display: flex;
    justify-content: space-between;
    max-width: 340px;
    margin-bottom: 15px;

    &__indicator {
        min-width: 17px;
        height: 17px;
        background: #00BF41;
        border-radius: 3px;
        margin-right: 15px;
        margin-top: 3px;
    }

    &__text {
        color: #393939;
    }

    &--finished {
        .Exerciese__text {
            color: #828282;
        }
    }
}