.lesson__li { 
    width: 100%;
    display: flex;
    margin-bottom: 5px;
}

.li {
    &__title {
        margin-left: 5px;
        margin-top: 4px;
        font-size: 15px;
        color: #235390;

        &:last-child {
            margin-bottom: 0;
        }

        &:hover {
            text-decoration: underline;
            cursor: pointer;
            color: #b3995d;
        }
    }

    &__img {
        margin-left: 0px;
        width: 30px;
        height: 30px;
        border-radius: 50%;
    }
}