.Dashboard {
    &-header {
        width: 100%;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        z-index: 2;
    }
    &-header__inner {
        display: flex;
        justify-content: space-between;
        align-items: center;
        background-color: #fff;
        padding: 10px 20px;
        border-bottom: 5px solid #235390;
        height: 65px;
        width: 100%;
        position: fixed;
        z-index: 100;
        margin: 0 auto;
    }
    &-nav {
        font-size: 14px;
        text-transform: uppercase;
        // max-width: 200px;
        width: 20%;
        align-items: center;
        display: flex;
        justify-content: flex-end;
    }
    
    &-nav__money {
        display: flex;
        justify-content: start;
    }
    
    &-nav__link {
        display: inline-block;
        margin: 0 15px;
        position: relative;
        color: #333;
        text-decoration: none;
        transition: color .2s linear;
    }
    
    &-nav__link:after {
        content: "";
        display: block;
        width: 100%;
        height: 1px;
        opacity: 0;
        background-color: #333;
        position: absolute;
        top: 100%;
        left: 0;
        z-index: 1;
        transition: opacity .2s linear;
    }
    
    &-nav__link:hover {
        color: chocolate;
    }
    
    &-nav__link:hover:after {
        opacity: 1;
    }
    
    &-nav__photo {
        cursor: pointer;
        width: 50px;
        height: 50px;
        border-radius: 50%;
        border: 1px solid #333;
    }
}
.money {
    width: 30px;
    margin: 0;
    height: 30px;
}

.money__count {
    font-size: 20px;
    margin-right: 15px;
    padding-top: 3px;
}

.Chess {
    .Dashboard {
        &-header__inner {
            position: absolute;
        }
    }
}

@import './DashboardHeader.media.scss';