@media (max-width: 500px) {
    .level {
        &__number {
            font-size: 18px;
        }
        &__title,
        &__button {
            font-size: 16px;
        }
    }
}

@media (max-width: 450px) {
    .level {
        &__title {
            display: none;
        }

        &__flex-wrapper {
            flex-wrap: wrap;
            justify-content: flex-end;
        }
    }
}