@import '../../css/mixins';

.Chess {
    background-image: linear-gradient(135deg, #fdfcfb 0%, #e2d1c3 100%);
    position: relative;

    button {
        cursor: pointer;
    }

    &__lesson-title {
        font-size: 36px;
        line-height: 42px;
        margin-bottom: 29px;
    }

    &__video-buttons {
        max-width: 650px;
        width: 100%;
        height: 50px;
        display: flex;
        justify-content: space-between;
        margin-bottom: 23px;
    }

    &__watch-history-button,
    &__watch-lesson-button {
        border: none;
        border-radius: 10px;
        max-width: 313px;
        width: 100%;
        font-size: 18px;
        line-height: 21px;
        transition: 0.3s;
        -webkit-transition: 0.3s;
        -moz-transition: 0.3s;
        -ms-transition: 0.3s;
        -o-transition: 0.3s;
    }

    &__watch-history-button {
        margin-right: 24px;
        background: #34F5D3;
    }

    &__watch-history-button:hover {
        background: #2cccaf;
    }

    &__watch-lesson-button {
        background: #FF515B;
    }

    &__watch-lesson-button:hover {
        background: #F33A45;
    }

    &__container {
        padding: 0 15px;
        max-width: 970px;
        margin: 0 auto;
    }

    &__decorative-wrapper {
        background-color: #fff;
        @include chess-border-radius;
        @include chess-border;
        padding: 25px 26px;
    }

    &__wrapper {
        display: flex;
        justify-content: space-around;
        @include chess-border-radius;
        @include chess-border;
        border-top-right-radius: 0px;
        border-bottom-right-radius: 0px;
        max-height: 522px;
        margin-bottom: 27px;
    }

    &__exercies-section {
        border-right: 1px solid #848484;
        max-width: 401px;
        color: #393939;
        width: 100%;
    }

    &__chessboard-section {}

    &__page {
        padding-top: 100px;
    }

    &__buttons {
        margin-top: auto;
        display: flex;
        justify-content: space-between;
        height: 50px;
    }

    &__prev-exercies-button,
    &__next-exercies-button {
        border: none;
        @include chess-border-radius;
        max-width: 450px;
        width: 100%;
        font-size: 18px;
        line-height: 21px;
        color: #393939;
        background: #FFF500;
        transition: 0.3s;
        -webkit-transition: 0.3s;
        -moz-transition: 0.3s;
        -ms-transition: 0.3s;
        -o-transition: 0.3s;
    }

    &__next-exercies-button--disabled,
    &__prev-exercies-button--disabled {
        opacity: 0.6;
    }

    &__prev-exercies-button:hover:not(.Chess__prev-exercies-button--disabled),
    &__next-exercies-button:hover:not(.Chess__next-exercies-button--disabled) {
        background: #e2da00;
    }


    &__prev-exercies-button {
        margin-right: 21px;
    }

    &__hint-button {
        width: 163px;
        background: #0E0E0E;
        border: 1px solid #0E0E0E;
        color: white;
    }

    @media (max-width: 770px) {
        &__buttons {
            height: 30px;
        }

        &__buttons button {
            font-size: 15px;
        }

        &__wrapper {
            margin-bottom: 15px;
        }

        &__decorative-wrapper {
            padding: 20px;
        }

        .ProgressBar {
            margin-bottom: 12px;
        }

        &__video-buttons {
            height: 35px;
            font-size: 17px;
        }

        @media (max-width: 550px) {
            &__wrapper {
                @include chess-border-radius;
                flex-direction: column;
                border-bottom-left-radius: 0px;
                border-bottom-right-radius: 0px;
                max-height: none;
            }

            &__exercies-section {
                border-right: none;
                border-bottom: 1px solid #848484;
            }

            &__decorative-wrapper {
                width: 366px;
                margin: 0 auto;
            }

            &__buttons {
                height: 46px;
            }

            &__video-buttons button {
                font-size: 14px;
            }
        }
    }
}